@tailwind base;
@tailwind components;
@tailwind utilities;

@keyframes marquee {
    0% { transform: translateX(100%); }
    100% { transform: translateX(-100%); }
  }

  .animate-marquee {
    display: inline-block;
    padding-left: 100%;
    animation: marquee 15s linear infinite;
  }

/* Ocultar scrollbar en navegadores WebKit (Chrome, Safari) */
.scrollbar-hide::-webkit-scrollbar {
  display: none;
}

/* Ocultar scrollbar en Firefox */
.scrollbar-hide {
  scrollbar-width: none; /* Firefox */
}


/* Aplica a los scrollbars dentro de la clase custom-scrollbar */
.custom-scrollbar::-webkit-scrollbar {
  width: 8px;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background-color: #1d4ed8; /* Color del scrollbar */
  border-radius: 4px;
}


.custom-scrollbar::-webkit-scrollbar-track {
  background-color: rgba(255, 255, 255, 0.1); /* Fondo del track */

  border-radius: 4px;
}

.jw-icon-fullscreen, .jw-icon-pip, .jw-icon-cc, .jw-text-elapsed {
  display: none !important;
}

.jw-icon:hover {
  color: white !important;
  background-color: #1d4ed8 !important;
  border-radius: 2px !important;
}
.jw-timesegment-progress, .jw-progress {
  background-color: #1d4ed8 !important;

}

#player iframe {

  width: 100%;
  height: 100vh; /* O la altura que desees */


}
